@use "abstracts" as *;

:root {
    @each $colour, $shades in $colours {
        @each $shade, $value in $shades {
            --clr-#{$colour}-#{$shade}: #{$value};
        }
    }
}

*,
*::before,
*::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

a {
    text-decoration: none;
}

@each $colour, $shades in $colours {
    @each $shade, $value in $shades {
        .bg-#{$colour}-#{$shade} {
            background-color: $value;
        }
    }
}

@each $colour, $shades in $colours {
    @each $shade, $value in $shades {
        .text-clr-#{$colour}-#{$shade} {
            color: $value;
        }
    }
}

body {
    font-family: "Roboto", sans-serif;
}

.title {
    height: 50vh;

    display: flex;
    flex-direction: column;
    
    h1,
    h2 {
        font-weight: 100;
        text-align: center;
    }

    h1 {
        margin-top: auto;
        font-size: 4.5rem;
        letter-spacing: 0.625rem;
    }

    h2 {
        margin-bottom: auto;
        font-size: 2rem;
        letter-spacing: 1.5rem;
    }
}

nav {
    position: sticky;
 
        ul {
            display: flex;
            list-style: none;
            padding: 1.25rem 0;
            
            li {
                &:first-child {
                    margin-left: auto;
                }

                &:last-child {
                    margin-right: auto;
                }
            }

            a {
                padding: 1rem 4rem;
                margin: 0 10px;
            
            &:hover,
            &:focus {
                background-color: var(--clr-primary-100);
                border-radius: 0.5rem;
            }
        }
    }
}

section {
    height: 75vh;
}